// vmvuethisvuextoken
// getApp().globalDatatokengetApp().globalData
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://jjsp.autovlog.com.cn',
		showLoading: true, // loading
		loadingMask: true, // loading

		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true, 
		// content-type
		header: {
			'content-type': 'application/json;charset=UTF-8'
		},
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// config.header.Token = 'xxxxxx';
		
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorageStorage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		
		// // urlurlthis.$u.get(url)url
		// if(config.url == '/user/login') config.header.noToken = true;
		// // configreturn
		// return config;
		// // returnfalse
		// // if(config.url == '/user/rest') return false; // 
		return config; 
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		console.log('interceptor',res);
		// console.log(Vue);
		// console.log(Vue.prototype);
		// console.log(Vue.prototype.$u);
		// console.log(Vue.prototype.$u.toast);
		// originalDatatrue
		// res.statueCoderes.data.code
		if(res.statusCode == 200) {
			// originalDatatruereturnthis.$u.postthen
			return res.data;  
		} else if(res.statusCode == 201){
			// 201token
			vm.$u.toast(res.message);
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/login/login')
			}, 1500)
			return false;
		} else {
			vm.$u.toast(res.message);
			return false
		}
		// return false;
	}
}

export default {
	install
}