// URL()
let hotSearchUrl = '/ebapi/store_api/hot_search';
let indexUrl = '/ebapi/public_api/index';
let loginApi = '/api/user/login';
let newsList = '/api/news/list'
let listNew = '/api/news/list_new'

// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// params
	let getSearch = (params = {}) => vm.$u.get(hotSearchUrl, {
		id: 2
	});
	
	// params
	let getInfo = (params = {}) => vm.$u.post(indexUrl, params);
	
	let login = (params = {}) => vm.$u.post(loginApi, params)
	let getList = (params = {}) => vm.$u.post(newsList, params)
	let getListNew = (params = {}) => vm.$u.post(listNew, params)
	
	// vm.$u.api(vmthisthis.$u.api)
	vm.$u.api = {getSearch, getInfo, login};
}

export default {
	install
}